@import './Variables.scss';

.top-menu {
    position: fixed;
    width: 100%;
    z-index: 998;
    margin: 0 auto;
    display: block;
    left: 0;
    top: 60px;

    .logo {
        float: left;
    } // END LOGO

    .right-menu {
        float: right;
        text-align: right;

        li {
            display: inline-block;
            vertical-align: middle;

            @media #{$mq-lg-max} {
                display: none;
            }

            &.sep {
                p {
                    padding: 0 20px;
                    color: $white;
                } // END P
            } // END SEP

            a.title-small {
                color: $white;
            } // END TITLE SMALL
        } // END LI

        .social {
            li {
                a {
                    display: inline-block;

                    svg {
                        display: inline-block;
                        width: 20px;
                        height: 20px;

                        path {
                            fill: $white;
                        } // END PATH
                    } // END SVG
                } // END A
            } // END LI
        } // END SOCIAL
    } // END RIGHT MENU
} // END TOP MENU